@import "src/assets/style/design.scss";

.rules-section {
  font-size: 20px;
  color: #000000; // Черный текст в светлой теме

  &__rules {
    margin-top: 16px;
  }

  &__rule {
    position: relative;

    padding-left: 16px;
    margin-top: 15px;
  }

  &__rule-stripe:only-child {
    display: none;
  }

  &__rule-stripe:not(:only-child) {
    content: "";
    position: absolute;
    left: 0;
    top: 0;

    display: block;
    width: 4px;
    height: 100%;
    background-color: $baseColor;

    &:before,
    &:after {
      content: "";
      position: absolute;
      top: -4px;
      left: -50%;

      display: block;
      width: 8px;
      height: 8px;
      background-color: $baseColor;

      border-radius: 24px;
    }

    &:after {
      top: unset;
      bottom: -5px;
    }
  }

  &__offpeak {
    margin-left: 10px;
  }

  &__rule-row {
    display: flex;

    min-height: 48px;
    margin-top: 10px;

    &:first-child {
      margin-top: 0;
    }
  }

  &__rule-row-select {
    width: 180px;
  }

  &__rule-row-field {
    width: 200px;
  }

  &__otstup {
    margin-left: 0px;
    margin-top: 10px;
  }

  &__rule-row-control-btn {
    margin-left: 10px;

    &:first-child {
      margin-left: 0;
    }
  }

  &__title {
    font-size: 1.2em;
    font-weight: 700;
    color: #000000; // Черный заголовок в светлой теме
  }

  &__subtitle {
    display: block;
    margin-top: 20px;

    font-weight: 700;
    font-size: 1.05em;
    color: #000000; // Черный подзаголовок в светлой теме
  }

  &__filter {
    display: flex;
    align-items: center;
    margin-top: 16px;
    max-width: 1000px;
  }

  &__elems-toggle {
    max-width: 764px;
    margin-top: 16px;

    &_KO {
      margin-left: 0 !important;
      margin-right: 8px;
      width: 65%;
    }

    &_effmu {
      margin-left: 0 !important;
      width: 100%;
    }

    &_status {
      margin-left: 8px !important;
    }
  }

  &__elems-toggle-divider {
    width: 4px;
    border-radius: 10px;
    margin: 0 8px;
    height: 36px;
    background-color: $baseColor;
  }

  &__filter &__elems-toggle {
    margin-top: 0;
    margin-left: 16px;
  }

  &__filter-network {
    margin-left: 16px;
    width: 205px;
  }
}

// Стили для темной темы
[data-theme="dark"] {
  .rules-section {
    color: #ffffff;

    &__title {
      color: #ffffff;
    }

    &__subtitle {
      color: #ffffff;
    }

    &__rule-stripe:not(:only-child) {
      background-color: #6b85ff;

      &:before,
      &:after {
        background-color: #6b85ff;
      }
    }

    &__elems-toggle-divider {
      background-color: #6b85ff;
    }
  }
}
